import axios from "axios";
import { useEffect, useRef, useState } from "react";
import ProductModal from "../../components/ProductModal";
import DeleteModal from "../../components/DeleteModal";
import Pagination from "../../components/Pagination";
import { Modal } from "bootstrap";
import { LoaderThreeDots } from "../../components/Loaders/LoaderSpinner";

function AdminProducts () {
  const [products, setProducts] = useState([]); // 
  const [pagination, setPagination] = useState({}); // 
  
  // type:  modal 
  const [type, setType] = useState('create'); //  modal  edit 
  const [tempProduct, setTempProduct] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  
  //  bs5
  const productModal = useRef(null);
  const deleteModal = useRef(null);
  
  useEffect(() => {
    // 
    productModal.current = new Modal('#productModal', {
      backdrop: 'static',
    });
    deleteModal.current = new Modal('#deleteModal', {
      backdrop: 'static',
    });
    
    getProducts();
  }, []);

  // 
  const getProducts = async (page = 1) => {
    try {
      setIsLoading(true); // 
      const productRes = await axios.get(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/products?page=${page}`);
      console.log(productRes);
      setProducts(productRes.data.products);
      setPagination(productRes.data.pagination); // 
    } catch (error) {
      console.error(":", error);
      // 
    } finally {
      setIsLoading(false); // 
    }
  };
  

  //   
  const openProductModal = (type, product) => {
    setType(type);
    setTempProduct(product);
    productModal.current.show();
  };
  const closeProductModal = () => {
    productModal.current.hide();
  };

  // 
  const openDeleteModal = (product) => {
    setTempProduct(product);
    deleteModal.current.show();
  };
  const closeDeleteModal = () => {
    deleteModal.current.hide();
  };
  //  
  const deleteProduct = async (id) => {
    try {
      const res = await axios.delete(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${id}`);
      if (res.data.success) { //  success: true 
        getProducts(); // 
        deleteModal.current.hide(); // 
      }
    } catch (error) {
      console.log(error);
    }
  }


  return (
    <div className="p-3">
      <ProductModal 
        closeProductModal={closeProductModal} 
        getProducts={getProducts}
        tempProduct={tempProduct}
        type={type}
      />
      <DeleteModal 
        closeDeleteModal={closeDeleteModal}
        text={tempProduct.title}
        handleDelete={deleteProduct}
        id={tempProduct.id}
        itemType="product"
      />
      <h3></h3>
      <hr />
      <div className="text-end mb-2">
        <button
          type="button"
          className="btn btn-primary btn-sm"
          onClick={() => openProductModal('create', {})}
        >
          
        </button>
      </div>
      <div className="table-responsive text-nowrap" >
        <table className="table">
          <thead>
            <tr>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {isLoading ? (
                <tr>
                  <td colSpan="10"> {/* 10 */}
                    <LoaderThreeDots />
                  </td>
                </tr>
              ) : products.map((product) => {
              return (
                <tr key={product.id}>
                  <td>
                    <img src={product.imageUrl} alt={product.title} width={100} className="object-cover"/>
                  </td>
                  <td>{product.title}</td>
                  <td>{product.category}</td>
                  <td>{product.price}</td>
                  <td>{product.is_enabled ? '' : ''}</td>
                  <td>
                    <button
                      type="button"
                      className="btn btn-primary btn-sm"
                      onClick={() => openProductModal('edit', product)}
                    >
                      
                    </button>
                    <button
                      type="button"
                      className="btn btn-outline-danger btn-sm ms-2"
                      onClick={() => openDeleteModal(product)}
                    >
                      
                    </button>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </div>
      <Pagination 
        pagination={pagination}
        changePage={getProducts}
      />
    </div>
  )
}

export default AdminProducts;