import { useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { Modal } from 'bootstrap';

function ChoiceModal({ show, onClose }) {
  const navigate = useNavigate();
  const modalRef = useRef(null); //  useRef  Modal 

  useEffect(() => {
    const modalElement = document.getElementById('choiceModal');
    modalRef.current = new Modal(modalElement, {
      backdrop: 'static'
    });

    return () => {
      if (modalRef.current) {
        modalRef.current.dispose(); //  Modal 
      }
    };
  }, []);

  useEffect(() => {
    //  `show` 
    if (show) {
      modalRef.current.show();
    } else {
      modalRef.current.hide();
    }
  }, [show]);

  const handleClose = () => {
    onClose();
  }

  return (
    <div id="choiceModal" className="modal fade" tabIndex="-1">
      <div className="modal-dialog modal-dialog-centered">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title"></h5>
            <button type="button" className="btn-close" onClick={handleClose}></button>
          </div>
          <div className="modal-body">
            <p></p>
          </div>
          <div className="modal-footer">
            <button type="button" className="btn btn-secondary" onClick={() => navigate('/myorders')}></button>
            <button type="button" className="btn btn-primary" onClick={() => navigate('/admin/products')}></button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ChoiceModal;
