import axios from "axios";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import ChoiceModal from "../components/ChoiceModal";

function Login() {
  const [data, setData] = useState({
    username: '',
    password: ''
  });
  const [loginState, setLoginState] = useState({});
  const [showModal, setShowModal] = useState(false); //  Modal 

  const handleChange = (e) => {
    const { name, value } = e.target;
    setData({ ...data, [name]: value });
  }

  const submit = async (e) => {
    e.preventDefault(); // 
    try {
      const res = await axios.post('/v2/admin/signin', data);
      const { token, expired } = res.data;
      document.cookie = `hexToken=${token}; expires=${new Date(expired)};`;
      // console.log(token);
      if (res.data.success) {
        console.log(res);

        setShowModal(true); // 
        setLoginState(res.data);
      }
    } catch (error) {
      console.log(error);
      setLoginState(error.response.data);
    }
  }

  useEffect(() => {
    const storedData = localStorage.getItem('loginData');
    if (storedData) {
      const { username, password, rememberMe } = JSON.parse(storedData);
      setData({ username, password });
      document.getElementById('rememberMe').checked = rememberMe;
    }
  }, []);
  

  return (
    <div className="LoginContainer">
      <div className="LoginCard">
      {/*  ChoiceModal  */}
        <ChoiceModal show={showModal} onClose={() => setShowModal(false)} />
        <div className="WelcomeBack">
          <h1 className="text-nowrap">
            Welcome Back!
            <i className="ms-3 bi bi-cup-hot-fill"></i>
          </h1>
        </div>
        <div className="LoginForm position-relative">
          {/*  */}
          {loginState.message && !loginState.success && (
            <div className="alert alert-danger login-error" role="alert">
              {loginState.message}
            </div>
          )}
          
          <h2></h2>
          <p className="mb-4 LoginForm-p">! </p>
          <form onSubmit={submit}>
            <div className="mb-3">
              <label htmlFor="username" className="form-label"></label>
              <input 
                type="text" 
                id="username"
                className="form-control" 
                placeholder="User Name"
                name="username" 
                value={data.username} 
                onChange={handleChange} 
              />
            </div>
            <div className="mb-3">
              <label htmlFor="password" className="form-label"></label>
              <input 
                type="password" 
                id="password"
                className="form-control" 
                placeholder="Password" 
                name="password" 
                value={data.password} 
                onChange={handleChange} 
              />
            </div>
            <div className="my-4 form-check">
              <input type="checkbox" className="form-check-input" id="rememberMe" />
              <label className="form-check-label" htmlFor="rememberMe"></label>
            </div>
            <button type="submit" className="btn btn-primary w-100"></button>
          </form>
          <div className="mt-3">
            <i className="bi bi-arrow-return-right me-2"></i>
            <Link to="/"></Link>
          </div>
        </div>
      </div>
      
    </div>
  );
}

export default Login;
