import { useForm } from 'react-hook-form';
import { Modal, Button, Form } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { createAsyncMessage } from '../slice/messageSlice';

const CancelOrderModal = ({ show, handleClose, orderInfo, onConfirmCancel }) => {
  const { register, handleSubmit, formState: { errors }, reset } = useForm();
  const dispatch = useDispatch();

  const onSubmit = (data) => {
    //  API 
    new Promise((resolve, reject) => {
      // 
      setTimeout(() => {
        const isCancelSuccess = true; // 
        if (isCancelSuccess) {
          resolve("");
          onConfirmCancel(orderInfo.id);
        } else {
          reject(new Error(""));
        }
      }, 1000); // 
    })
    .then((message) => {
      // 
      dispatch(createAsyncMessage({
        success: true,
        id: new Date().getTime(), // ID
        message: message,
      }));
      handleClose(); // 
      reset(); // 
    })
    .catch((error) => {
      //  createAsyncMessage
      dispatch(createAsyncMessage({
        success: false,
        message: error.message,
      }));
      alert(error.message); //  alert
    });
  };
  

  return (
    <Modal show={show} onHide={handleClose} backdrop='static'>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Group className="mb-3">
            <Form.Label></Form.Label>
            <Form.Control type="text" {...register("orderId")} defaultValue={orderInfo?.id} readOnly />
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label></Form.Label>
            <Form.Select {...register("reason", { required: "" })}
              onChange={(event) => {
                event.stopPropagation(); // 
                // 
              }}
            >
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </Form.Select>
            {errors.reason && <p className="text-danger">{errors.reason.message}</p>}
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label></Form.Label>
            <Form.Control as="textarea" rows={3} {...register("message")} />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}></Button>
          <Button variant="primary" className="bg-danger" type="submit"></Button>
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default CancelOrderModal;
