import { useEffect, useRef, useState } from 'react';
import axios from "axios";
import OrderModal from "../../components/OrderModal";
import Pagination from "../../components/Pagination";
import { Modal } from "bootstrap";
import { LoaderThreeDots } from '../../components/Loaders/LoaderSpinner';
import DeleteModal from '../../components/DeleteModal';

function AdminOrders() {
  const [orders, setOrders] = useState([]);
  const [pagination, setPagination] = useState({});
  const [tempOrder, setTempOrder] = useState({});
  const [isLoading, setIsLoading] = useState(false);

  //  bs5
  const orderModal = useRef(null);
  const deleteModal = useRef(null);

  useEffect(() => {
    orderModal.current = new Modal('#orderModal', {
      backdrop: 'static',
    });

    deleteModal.current = new Modal('#deleteModal', {
      backdrop: 'static',
    });

    getOrders();
  }, []);

  const getOrders = async (page = 1) => {
    try {
      setIsLoading(true);
      const res = await axios.get(
        `/v2/api/${process.env.REACT_APP_API_PATH}/admin/orders?page=${page}`,
      );
      console.log(res);
      const validOrders = res.data.orders.filter(order => order.id); //  id
      setOrders(validOrders);
      setPagination(res.data.pagination);
    } catch (error) {
      console.error(":", error);
      // 
    } finally {
      setIsLoading(false); // 
    }
  };

  // 
  const openDeleteModal = (Order) => {
    setTempOrder(Order);
    deleteModal.current.show();
  };
  const closeDeleteModal = () => {
    deleteModal.current.hide();
  };
  //  
  const deleteOrder = async (id) => {
    try {
      const res = await axios.delete(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/order/${id}`);
      console.log("deleteOrder",res);
      if (res.data.success) { //  success: true 
        getOrders(); // 
        deleteModal.current.hide(); // 
      }
    } catch (error) {
      console.log(error);
    }
  }

  const openOrderModal = (order) => {
    setTempOrder(order);
    orderModal.current.show();
  }
  const closeOrderModal = () => {
    setTempOrder({});
    orderModal.current.hide();
  }

  return (
    <div className='p-3'>
      <OrderModal
        closeProductModal={closeOrderModal}
        getOrders={getOrders}
        tempOrder={tempOrder}
      />
      <DeleteModal 
        closeDeleteModal={closeDeleteModal}
        text={tempOrder.user?.email}
        handleDelete={deleteOrder}
        id={tempOrder.id}
        itemType="order"
      />
      <h3></h3>
      <hr />
      <div className="table-responsive text-nowrap" >
        <table className='table'>
          <thead>
            <tr>
              <th scope='col'> id</th>
              <th scope='col'></th>
              <th scope='col'></th>
              <th scope='col'></th>
              <th scope='col'></th>
              <th scope='col'></th>
              <th scope='col'></th>
              <th scope='col'></th>
            </tr>
          </thead>
          <tbody>
            {isLoading ? (
                <tr>
                  <td colSpan="10"> {/*  */}
                    <LoaderThreeDots  />
                  </td>
                </tr>
              ) : orders.map((order) => {
              return (
                <tr key={order.id}>
                  <td>{order.id}</td>
                  <td>
                    {order.user?.email}
                  </td>
                  <td>${order.total}</td>
                  <td>
                    {order.is_paid ? (
                      <span className='text-success fw-bold'></span>
                    ) : (
                      ''
                    )}
                  </td>
                  <td>
                    {order.paid_date
                      ? new Date(order.paid_date * 1000).toLocaleString()
                      : ''}
                  </td>
                  <td>{order.message}</td>
                  <td>
                    <button
                      type='button'
                      className='btn btn-primary btn-sm'
                      onClick={() => {
                        openOrderModal(order);
                      }}
                    >
                      
                    </button>
                  </td>
                  <td>
                    <button
                      type='button'
                      className='btn btn-outline-danger btn-sm'
                      onClick={() => openDeleteModal(order)}
                    >
                      
                    </button>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
      <Pagination pagination={pagination} changePage={getOrders} />
    </div>
  );
}

export default AdminOrders;