import axios from "axios";
import { useEffect, useReducer } from "react";
import { Link, Outlet, useNavigate } from "react-router-dom";
import Message from "../../components/Message";
import { MessageContext, messageReducer, initState } from "../../store/messageStore";

function Dashboard() {
  const navigate = useNavigate();
  const reducer = useReducer(messageReducer, initState);

  // 
  const logout = () => {
    document.cookie = 'hexToken=;' //  Cookie;
    navigate('/login');
  };

  //  Cookie  token
  const getCookie = (name) => {
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(';').shift();
  };

  const token = getCookie('hexToken'); // hexToken Cookie
  axios.defaults.headers.common['Authorization'] = token;
  // console.log(token);

  //  token
  useEffect(() => {
    //  token 
    if (!token) {
      return navigate('/login');
    }
    //  token 
    (async () => {
      try {
        await axios.post('/v2/api/user/check');
      } catch (error) {
        console.log(error);
        if(!error.response.data.success) {
          navigate('/login');
        }
      }
    })();
  }, [navigate, token]);

  useEffect(() => {
    document.body.style.overflow = 'auto';
    // 
    return () => {
      document.body.style.overflow = '';
      document.body.style.overflow = 'auto';
    };
  }, []);
  

  return (
    <MessageContext.Provider value={reducer}>{/*  */}
      <Message />
      <nav className="navbar navbar-expand-lg bg-dark">
        <div className="container-fluid">
          <p className="text-white mb-0">
            Kyle's iSelect 
          </p>
          <div className="ms-3 text-light">
            <i className="bi bi-arrow-return-right me-2"></i>
            <Link to="/" className="text-light"></Link>
          </div>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon" />
          </button>
          <div className="collapse navbar-collapse justify-content-end" id="navbarNav">
            <ul className="navbar-nav">
              <li className="nav-item">
                <button type="button" className="btn btn-sm btn-light"
                  onClick={logout}>
                  
                </button>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      <div className="Dashboard-container">
        {/**  */}
        <div className="sticky-top bg-light">
          <ul className="Dashboard-list list-group list-group-horizontal list-group-horizontal-sm text-nowrap">
            <Link className="list-group-item list-group-item-action py-3" to="/admin/products">
              <i className="bi bi-cup-fill me-2" />
              <span className="d-inline-block">
                
              </span>
            </Link>
            <Link className="list-group-item list-group-item-action py-3" to="/admin/coupons">
              <i className="bi bi-ticket-perforated-fill me-2" />
              <span className="d-inline-block">
                
              </span>
            </Link>
            <Link className="list-group-item list-group-item-action py-3" to="/admin/orders">
              <i className="bi bi-receipt me-2" />
              <span className="d-inline-block">
                
              </span>
            </Link>
          </ul>
        </div>
        <div className="Dashboard-table-width">
          {/* Products */}
          { token && <Outlet />}
        </div>
      </div>
    </MessageContext.Provider>
  )
}

export default Dashboard;