import { Link, useOutletContext, useNavigate, useLocation } from "react-router-dom";
import { useForm } from "react-hook-form";
import { Input } from "../../components/FormElements";
import axios from "axios";
import { useEffect, useState } from "react";
import PayPalButton from "../../components/Payment/PayPalButton";
import { LoaderPropagateLoader } from "../../components/Loaders/ReactSpinners";


function Checkout() {
  const { cartData, getCart } = useOutletContext();
  const location = useLocation();
  const navigate = useNavigate();

  console.log(cartData);
  const [isLoading, setIsLoading] = useState(false);
  const [isSameAsContact, setIsSameAsContact] = useState(false);
  const [selectedPaymentMethod, setSelectedPaymentMethod] = useState("");
  const { couponDiscount } = location.state || {}; // state
  // 
  const {
    register,
    handleSubmit,
    setValue,
    getValues, //  useForm  getValues 
    watch,
    formState: { errors }
  } = useForm();

  // 
  const paymentMethod = watch("paymentMethod"); // PayPal
  const contactInfo = watch(['name', 'tel', 'address']);

  useEffect(() => {
    if (isSameAsContact) {
      // 
      setValue('shippingName', contactInfo[0]);
      setValue('shippingTel', contactInfo[1]);
      setValue('shippingAddress', contactInfo[2]);
    }
  }, [isSameAsContact, contactInfo, setValue]);
  

  useEffect(() => { //  useEffect  watch('paymentMethod') 
    setSelectedPaymentMethod(paymentMethod || "cashOnDelivery");
  }, [paymentMethod]);
  

  // 
  const handlePaymentSuccess = async (paymentDetails) => {
    setIsLoading(true);
    const formValues = getValues();
    const { name, email, tel, address} = formValues;
    console.log('formValues',formValues);
    console.log('paymentDetails',paymentDetails);
    //  data 
    const formdata = {
      "data": {
        "user": {
          name, 
          email, 
          tel, 
          address,
        },
        "message": `PayPal ID: ${paymentDetails.id}`,
      }
    };
    //  onSubmit 
    await onSubmit(formdata, true);
  };


  const onSubmit = async (data, isPaymentSuccess = false) => {
    console.log(" isPayPalSuccess:", isPaymentSuccess);
    // 
    if (typeof isPaymentSuccess !== 'object' && isPaymentSuccess === true) { //  react-hook-form  handleSubmit  onSubmit  event 
      try {
        const Paymentres = await axios.post(
          `/v2/api/${process.env.REACT_APP_API_PATH}/order`,
          data,
        );
        console.log('isPaymentSuccess', Paymentres);

        //  Paymentres  orderId
        const orderId = Paymentres.data.orderId;
        console.log('OrderId:', orderId);

        //  orderId 
        const PayRes = await axios.post(
          `/v2/api/${process.env.REACT_APP_API_PATH}/pay/${orderId}`,
        );
        console.log('PayRes:', PayRes);
        await getCart(); // 
        setIsLoading(false);
        // navigate(`/success/${orderId}`);
        navigate(`/success/${orderId}`, { state: { paymentMethod: selectedPaymentMethod } });
      } catch (error) {
        console.error('Payment Submission Failed:', error);
      } finally {
        setIsLoading(false);
      }
    } else {
      try {
        const { name, email, tel, address } = data; // id
        const formData = { // 
          "data": {
            "user": {
              name, 
              email, 
              tel, 
              address,
            },
          }
        };

        const res = await axios.post(
          `/v2/api/${process.env.REACT_APP_API_PATH}/order`,
          formData,
        );
        console.log('', res);
        await getCart(); // 
        setIsLoading(false);
        navigate(`/success/${res.data.orderId}`, { state: { paymentMethod: selectedPaymentMethod } });
      } catch (error) {
        console.log(error)
      } finally {
        setIsLoading(false);
      }
    }
  }

  return(
    <>
      <div className="bg-light mt-6 pt-5 pb-7">
        {isLoading ? <LoaderPropagateLoader /> : (
          <div className="container">
            <div className="row justify-content-center flex-md-row flex-column-reverse">
              <div className="col-md-6">
                <div className="bg-white p-4">
                  <form onSubmit={handleSubmit(onSubmit)}>
                    <h4 className="fw-bold">1. </h4>
                    <div className="bg-white p-4 mb-3">
                      <div className="mb-2">
                        <Input
                          id='email'
                          labelText='Email*'
                          type='email'
                          errors={errors}
                          register={register}
                          rules={{
                            required: 'Email ',
                            pattern: {
                              value: /^\S+@\S+$/i,
                              message: 'Email ',
                            },
                          }}
                        ></Input>
                      </div>
                      <div className="mb-2">
                        <Input
                          id='name'
                          type='text'
                          errors={errors}
                          labelText='*'
                          register={register}
                          rules={{
                            required: '',
                            maxLength: {
                              value: 10,
                              message: ' 10',
                            },
                          }}
                        ></Input>
                      </div>
                      <div className="mb-2">
                      <Input
                          id='tel'
                          labelText='*'
                          type='tel'
                          errors={errors}
                          register={register}
                          rules={{
                            required: '',
                            minLength: {
                              value: 6,
                              message: ' 6 '
                            },
                            maxLength: {
                              value: 12,
                              message: ' 12 '
                            }
                          }}
                        ></Input>
                      </div>
                      <div className='mb-3'>
                        <Input
                          id='address'
                          labelText='*'
                          type='address'
                          errors={errors}
                          register={register}
                          rules={{
                            required: '',
                          }}
                        ></Input>
                      </div>
                      <div className="checkbox">
                        <label>
                          <input
                            type="checkbox"
                            checked={isSameAsContact}
                            onChange={e => setIsSameAsContact(e.target.checked)}
                          /> 
                        </label>
                      </div>
                    </div>
                    <h4 className="fw-bold">2. </h4>
                    <div className="bg-white p-4 mb-3">
                      <div className="mb-2">
                        <Input
                          id='shippingName'
                          type='text'
                          errors={errors}
                          labelText='*'
                          register={register}
                          rules={{
                            required: '',
                            maxLength: {
                              value: 10,
                              message: ' 10',
                            },
                          }}
                        ></Input>
                      </div>
                      <div className="mb-2">
                      <Input
                          id='shippingTel'
                          labelText='*'
                          type='tel'
                          errors={errors}
                          register={register}
                          rules={{
                            required: '',
                            minLength: {
                              value: 6,
                              message: ' 6 '
                            },
                            maxLength: {
                              value: 12,
                              message: ' 12 '
                            }
                          }}
                        ></Input>
                      </div>
                      <div className='mb-3'>
                        <Input
                          id='shippingAddress'
                          labelText='*'
                          type='address'
                          errors={errors}
                          register={register}
                          rules={{
                            required: '',
                          }}
                        ></Input>
                      </div>
                      <p className="mt-4 mb-2"></p>
                      <div className="form-check mb-2">
                        <input 
                          className="form-check-input" 
                          type="radio" 
                          name="paymentMethod" 
                          id="gridRadios1" 
                          value="cashOnDelivery" 
                          {...register("paymentMethod")} // 
                          defaultChecked 
                        />
                        <label className="form-check-label text-muted" htmlFor="gridRadios1">
                          
                        </label>
                      </div>
                      <div className="form-check mb-2">
                        <input 
                          className="form-check-input" 
                          type="radio" 
                          name="paymentMethod" 
                          id="gridRadios2" 
                          value="paypal" 
                          {...register("paymentMethod")} // 
                        />
                        <label className="form-check-label text-muted" htmlFor="gridRadios2">
                          PayPal
                        </label>
                      </div>
                      {/*  PayPalButton  paymentMethod  */}
                      {selectedPaymentMethod === 'paypal' && 
                        <div className="w-100">
                          <PayPalButton 
                            onPaymentSuccess={handlePaymentSuccess} 
                            total={cartData.total} 
                          />
                        </div>
                      }
                    </div>
                    <div className="d-flex mt-4 justify-content-between align-items-end w-100">
                      <Link to='/cart' className='text-dark mt-md-0 text-nowrap'>
                        <i className='bi bi-chevron-left me-2'></i> 
                      </Link>
                      {selectedPaymentMethod === 'paypal' ? '' :
                        <button
                          type='submit'
                          className='btn btn-dark py-3 px-5 rounded-0 text-nowrap'
                        >
                          
                        </button>
                      }
                    </div>
                  </form>
                </div>
              </div>
              <div className="col-md-6">
                <div className="border p-4 mb-4">
                  <h4 className="mb-4"></h4>
                  { cartData?.carts?.map((item) => {
                    return (
                      <div className="d-flex pb-2" key={item.id}>
                        <img 
                          src={item.product.imageUrl} 
                          alt="" className="me-2" 
                          style={{
                            width: '48px', 
                            height: '48px', 
                            objectFit: 'cover'
                          }} 
                        />
                        <div className="w-100">
                          <div className="d-flex justify-content-between fw-bold">
                            <p className="mb-0">{item.product.title}</p>
                            <p className="mb-0">x{item.qty}</p>
                          </div>
                          <div className="d-flex justify-content-between">
                            <p className="text-muted mb-0"><small>NT${item.product.price}</small></p>
                            <p className="mb-0">NT${item.final_total}</p>
                          </div>
                        </div>
                      </div>
                    )
                  })}
                  <table className="table mt-4 border-top border-bottom text-muted">
                    <tbody className="table-light">
                      <tr>
                        <th scope="row" className="border-0 px-0 pt-4 pb-0 font-weight-normal"></th>
                        <td className="text-end border-0 px-0 pt-4 pb-0">NT$ {cartData.total}</td>
                      </tr>
                      <tr>
                        <th scope="row" className="border-0 px-0 font-weight-normal">:</th>
                        <td className="text-end border-0 px-0">
                          {couponDiscount ? (
                            <span>NT$ -{couponDiscount}</span>
                          ) : (
                            <span></span>
                          )}
                        </td>
                      </tr>
                      <tr>
                        <th scope="row" className="border-0 px-0 pt-3 pb-4 font-weight-normal"></th>
                        <td className="text-end border-0 px-0 pt-3 pb-4">{selectedPaymentMethod === "cashOnDelivery" ? "" : "PayPal"}</td>
                      </tr>
                    </tbody>
                  </table>
                  <div className="d-flex justify-content-between mt-4">
                    <p className="mb-0 h4 fw-bold"></p>
                    <p className="mb-0 h4 fw-bold">NT${cartData.final_total}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </>
  )
}

export default Checkout;
