import axios from "axios";
import { useEffect, useState } from "react";

function CouponModal ({ closeModal, getCoupons, type, tempCoupon }) {
  const [tempData, setTempData] = useState({
    "title": "",
    "is_enabled": 1,
    "percent": 80,
    "due_date": 1555459200,
    "code": "testCode"
  });
  const [date, setDate] = useState(new Date());


  useEffect(() => {
    if (type === 'create') {
      setTempData({
        "title": "",
        "is_enabled": 1,
        "percent": 0,
        "due_date": 0,
        "code": ""
      });
      setDate(new Date()); // 
    } else if (type === 'edit') {
      setTempData(tempCoupon);
      setDate(new Date(tempCoupon.due_date)) //  due_date 
    }
  }, [type, tempCoupon]);


  const handleChange = (e) => {
    const { name, value, type: inputType, checked } = e.target; // 

    if (inputType === 'checkbox') {
      setTempData((prevData) => ({
        ...prevData,
        [name]: checked ? 1 : 0,
      }));
    } else if (inputType === 'number' || name === 'percent') {
      let numberValue = parseInt(value, 10);
      // 100
      numberValue = numberValue > 100 ? 100 : numberValue;
      setTempData((prevData) => ({
        ...prevData,
        [name]: numberValue,
      }));
    } else {
      setTempData((prevData) => ({
        ...prevData,
        [name]: value,
      }));
    }
  };


  const submit = async () => {
    try {
      // api 
      let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon`;
      let method = 'post';

      //   
      if (type === 'edit') {
        api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/coupon/${tempCoupon.id}`
        method = 'put';
      }

      const res = await axios[method](
        api , {
          data: {
            ...tempData,
            due_date: date.getTime() // new Dateunix timestamp
          }
        }
      );

      console.log('Submitting data:', {
        data: {
          ...tempData,
          due_date: date.getTime(),
        }
      });
      

      console.log(res);
      closeModal(); // 
      getCoupons(); // 
    } catch (error) {
      console.log(error);
    }
  }


  return (
    <div
      className='modal fade'
      tabIndex='-1'
      id="productModal"
      aria-labelledby='exampleModalLabel'
      aria-hidden='true'
    >
      <div className='modal-dialog modal-lg'>
        <div className='modal-content'>
          <div className='modal-header'>
            <h1 className='modal-title fs-5' id='exampleModalLabel'>
              { type === 'create' ? '' : ` ${tempData.title}` }
            </h1>
            <button
              type='button'
              className='btn-close'
              aria-label='Close'
              onClick={closeModal}
            />
          </div>
          <div className='modal-body'>
            <div className='mb-2'>
              <label className='w-100' htmlFor='title'>
                
                <input
                  type='text'
                  id='title'
                  placeholder=''
                  name='title'
                  className='form-control mt-1'
                  value={tempData.title}
                  onChange={handleChange}
                />
              </label>
            </div>
            <div className='row'>
              <div className='col-md-6 mb-2'>
                <label className='w-100' htmlFor='percent'>
                  %
                  <input
                    type='text'
                    name='percent'
                    id='percent'
                    placeholder='%'
                    className='form-control mt-1'
                    value={tempData.percent}
                    onChange={handleChange}
                    min="0"
                    max="100"
                  />
                </label>
              </div>
              <div className='col-md-6 mb-2'>
                <label className='w-100' htmlFor='due_date'>
                  
                  <input
                    type='date'
                    id='due_date'
                    name='due_date'
                    placeholder=''
                    className='form-control mt-1'
                    value={`${date.getFullYear().toString()}-${(
                      date.getMonth() + 1
                    )
                      .toString()
                      .padStart(2, 0)}-${date
                      .getDate()
                      .toString()
                      .padStart(2, 0)}`}
                    onChange={(e) => {
                      setDate(new Date(e.target.value));
                    }}
                  />
                </label>
              </div>
              <div className='col-md-6 mb-2'>
                <label className='w-100' htmlFor='code'>
                  
                  <input
                    type='text'
                    id='code'
                    name='code'
                    placeholder=''
                    className='form-control mt-1'
                    value={tempData.code}
                    onChange={handleChange}
                  />
                </label>
              </div>
            </div>
            <label className='form-check-label' htmlFor='is_enabled'>
              <input
                className='form-check-input me-2'
                type='checkbox'
                id='is_enabled'
                name='is_enabled'
                checked={!!tempData.is_enabled}
                onChange={handleChange}
              />
              
            </label>
          </div>
          <div className='modal-footer'>
            <button type='button' onClick={closeModal} className='btn btn-secondary'>
              
            </button>
            <button type='button' className='btn btn-primary'
              onClick={submit}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CouponModal;