import axios from "axios";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useParams, useLocation } from "react-router-dom";
import { LoaderPulseLoader } from "../../components/Loaders/ReactSpinners";

function CartSuccess() { 
  const { orderId } = useParams();
  const { state } = useLocation();
  const [isLoading, setIsLoading] = useState(true);
  const [orderData, setOrderData] = useState({});
  const paymentMethod = state?.paymentMethod;

  const getCart = async(orderId) => {
    const res = await axios.get(
      `/v2/api/${process.env.REACT_APP_API_PATH}/order/${orderId}`,
      );
      console.log('',res);
      setOrderData(res.data.order); //  orderData 
  }

  useEffect(() => {
    getCart(orderId);

    setTimeout(() => {
      setIsLoading(false);
    }, 1500);
  }, [orderId]);

  return (
    <>
      <div className="container mt-10 pb-5">
        {isLoading ? <LoaderPulseLoader /> : (
          <div className="mt-5">
            <div className="row">
              <div className="col-12">
                <h2 className="mb-4">
                  <i className="bi bi-check-circle-fill me-3"></i>
                </h2>
              </div>
            </div>
            <div className="row flex-column-reverse flex-md-row">
              <div className="col-md-6">
                <p className="h5">
                  <Link 
                    to="/myorders" 
                    className="ms-2 text-info fw-bold text-decoration-underline"
                  >
                    {orderData.id}
                  </Link>
                </p>
                <p className="h5">
                  <Link 
                    to="/myorders" 
                    className="mx-2 text-info fw-bold text-decoration-underline"
                  >
                    
                  </Link>
                </p>
                <h3 className="mt-5"></h3>
                  <p className="mb-0 d-inline-block me-3">
                    {paymentMethod === 'cashOnDelivery' ? '' : 'PayPal'}
                  </p>
                  {orderData.is_paid ? (
                    <span className='badge bg-primary fw-bold'></span>
                  ) : (
                    <span className='badge bg-warning text-dark rounded-pill'></span>
                  )}
                <h3 className="mt-4"></h3>
                <p>{orderData.user?.address}</p>
                <p className="fw-bold mb-5">3-5 </p>
                <p>
                  <br />
                  <br />
                  <br/><br/>
                  <br/>
                  
                </p>
                <p>
                  
                  <Link 
                    to="/contact-us" 
                    className="mx-2 text-info fw-bold text-decoration-underline"
                  >
                    
                  </Link>
                </p>
                <Link to="/" className="btn btn-outline-dark me-2 rounded-0 mb-4">
                  
                </Link>
              </div>
              <div className="col-md-6 mb-4">
                <div className="card rounded-0 py-4">
                  <div className="card-header border-bottom-0 bg-white px-4 py-0">
                    <h3></h3>
                  </div>
                  <div className="card-body px-4 py-0">
                    <ul className="list-group list-group-flush">
                      {Object.values(orderData?.products || {}).map((item) => {
                        return (
                          <li className="list-group-item px-0" key={item.id}>
                            <div className="d-flex mt-2">
                              <img 
                                src={item.product.imageUrl} 
                                alt="" 
                                className="me-2" 
                                style={{width: '60px', height: '60px', objectFit: 'cover'}} 
                              />
                              <div className="w-100 d-flex flex-column">
                                <div className="d-flex justify-content-between fw-bold">
                                  <h5>{item.product.title}</h5>
                                  <p className="mb-0">x{item.qty}</p>
                                </div>
                                <div className="d-flex justify-content-between mt-auto">
                                  <p className="text-muted mb-0"><small>NT${item.product.price}</small></p>
                                  <p className="mb-0">NT${item.final_total}</p>
                                </div>
                              </div>
                            </div>
                          </li>
                        )
                      })}
                      <li className="list-group-item px-0 pb-0">
                        <table className="table text-muted">
                          <tbody>
                            <tr>
                              <th scope="row" className="border-0 px-0 font-weight-normal"></th>
                              <td className="text-end border-0 px-0">NT$ {orderData.total}</td>
                            </tr>
                            <tr>
                              <th scope="row" className="border-0 px-0 pt-0 font-weight-normal"></th>
                              <td className="text-end border-0 px-0 pt-0">
                                {paymentMethod === 'cashOnDelivery' ? '' : 'PayPal'}
                              </td>
                            </tr>
                          </tbody>
                        </table>
                        <div className="d-flex justify-content-between mt-2">
                          <p className="mb-0 h4 fw-bold"></p>
                          <p className="mb-0 h4 fw-bold">NT$ {orderData.total}</p>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </>
  )
}

export default CartSuccess;