import axios from "axios";
import { useContext, useEffect, useRef, useState } from "react";
import { MessageContext, handleSuccessMessage, handleErrorMessage } from "../store/messageStore";
import { LoaderGrid } from "./Loaders/LoaderSpinner";



function ProductModal ({ closeProductModal, getProducts, type, tempProduct }) {
  const [tempData, setTempData] = useState({
    "title": "",
    "category": "",
    "origin_price": 100,
    "price": 300,
    "unit": "",
    "description": "",
    "content": "",
    "is_enabled": 1,
    "imageUrl": "",
    "imagesUrl": [],
  });

  const [uploadPreviewUrl, setUploadPreviewUrl] = useState("");
  const [isMainImageLoading ,setIsMainImageLoading] = useState(false);
  const [isOtherImagesLoading ,setIsOtherImagesLoading] = useState(false);
  const [message, dispatch] = useContext(MessageContext);
  const fileInputRef = useRef(null);
  const multipleFilesInputRef = useRef(null);

  useEffect(() => {
    if (type === 'create') {
      setTempData({
        "title": "",
        "category": "",
        "origin_price": 0,
        "price": 0,
        "unit": "",
        "description": "",
        "content": "",
        "is_enabled": 1,
        "imageUrl": "",
        "imagesUrl": [],
      });
    } else if (type === 'edit' && tempProduct) {
      //  imagesUrl 
      const imagesUrl = Array.isArray(tempProduct.imagesUrl) ? tempProduct.imagesUrl : [];
      setTempData({ ...tempProduct, imagesUrl });
    }
  }, [type, tempProduct]);

  // 
  const addImageUrlInput = () => {
    const newImagesUrls = [...tempData.imagesUrl, ''];
    setTempData({ ...tempData, imagesUrl: newImagesUrls });
  };
  // 
  const handleImageUrlChange = (index, event) => {
    const newImagesUrls = [...tempData.imagesUrl];
    newImagesUrls[index] = event.target.value;
    setTempData({ ...tempData, imagesUrl: newImagesUrls });
  };


  const handleChange = (e) => {
    const { value, name } = e.target;
  
    if (['price', 'origin_price'].includes(name)) {
      // 
      if (/^\d*\.?\d*$/.test(value)) {
        setTempData({
          ...tempData,
          [name]: Number(value),
        });
      }
    } else if (name === 'is_enabled') {
      setTempData({
        ...tempData,
        [name]: +e.target.checked, // boolean to 1 or 0
      });
    } else {
      setTempData({
        ...tempData,
        [name]: value,
      });
    }
  };
  

  // URL
  const clearUploadPreview = () => {
    setUploadPreviewUrl("");
  }

  // 
  const handleFileChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;
  
    // 
    const validTypes = ['image/jpeg', 'image/jpg', 'image/png'];
    if (!validTypes.includes(file.type)) {
      alert(' JPGJPEG  PNG');
      return;
    }
  
    // 3MB
    const maxSize = 3 * 1024 * 1024;
    if (file.size > maxSize) {
      alert(' 3MB');
      return;
    }

    const reader = new FileReader();
    reader.onload = (loadEvent) => {
      //  URL
      setUploadPreviewUrl(loadEvent.target.result);
    };
    reader.readAsDataURL(file); //  Data URL
  
    //  FormData 
    const formData = new FormData();
    formData.append('file-to-upload', file);

    setIsMainImageLoading(true); // 
  
    try {
      const response = await axios.post(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/upload`, formData);
      
      for (let [key, value] of formData.entries()) {
        console.log(key, value);
      }
      
      //  URL 
      if (response.data.imageUrl) {
        setTempData({ ...tempData, imageUrl: response.data.imageUrl });
        clearUploadPreview(); // 
      }
    } catch (error) {
      console.error('', error);
      // 
    } finally {
      setIsMainImageLoading(false); //  false
    }
  };

  // 
  const handleMultipleFilesChange = async (e) => {
    const files = e.target.files;
    if (!files.length) return;
  
    // URL
    const uploadedImagesUrls = [];
  
    // 
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      
      // 
      if (!['image/jpeg', 'image/jpg', 'image/png'].includes(file.type)) {
        alert(' JPGJPEG  PNG');
        continue; // 
      }
      if (file.size > 3 * 1024 * 1024) {
        alert(' 3MB');
        continue; // 
      }

      setIsOtherImagesLoading(true); // 
  
      try {
        // FormData
        const formData = new FormData();
        formData.append('file-to-upload', file);
  
        // API
        const response = await axios.post(`/v2/api/${process.env.REACT_APP_API_PATH}/admin/upload`, formData);
        
        // URL
        if (response.data.imageUrl) {
          uploadedImagesUrls.push(response.data.imageUrl);
        }
      } catch (error) {
        console.error(` ${file.name} `, error);
      } finally {
        setIsOtherImagesLoading(false); //  false
      }
    }
  
    // tempDataURL
    setTempData({ ...tempData, imagesUrl: [...tempData.imagesUrl, ...uploadedImagesUrls] });
  };
  

  const submit = async () => {
    try {
      // api 
      let api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product`;
      let method = 'post';

      //   
      if (type === 'edit') {
        api = `/v2/api/${process.env.REACT_APP_API_PATH}/admin/product/${tempProduct.id}`
        method = 'put';
      }

      const res = await axios[method](
        api , {
          data: tempData
        }
      );
      console.log(res);
      handleSuccessMessage(dispatch, res); //  messageStore.js
      handleCloseAndClear(); // 
      getProducts(); // 
    } catch (error) {
      console.log(error);
      handleErrorMessage(dispatch, error); //  messageStore.js
    }
  }
  
  // 
  const handleCloseAndClear = () => {
    // 
    if (fileInputRef.current) {
      fileInputRef.current.value = "";
    }

    // 
    if (multipleFilesInputRef.current) {
        multipleFilesInputRef.current.value = "";
    }

    // URL
    setUploadPreviewUrl("");

    //  closeProductModal 
    closeProductModal();
  };


  return (
    <div
      className='modal fade'
      tabIndex='-1'
      id="productModal"
      aria-labelledby='exampleModalLabel'
      aria-hidden='true'
    >
      <div className='modal-dialog modal-xl'>
        <div className='modal-content'>
          <div className='modal-header'>
            <h1 className='modal-title fs-5' id='exampleModalLabel'>
              { type === 'create' ? '' : ` ${tempData.title}` }
            </h1>
            <button
              type='button'
              className='btn-close'
              aria-label='Close'
              onClick={handleCloseAndClear}
            />
          </div>
          <div className='modal-body'>
            <div className='row'>
              <div className="col-sm-3">
                <div className='form-group mb-2'>
                  <h4></h4>
                  <label className='w-100 mb-1' htmlFor='image'>
                    
                    <input
                      type='text'
                      name='imageUrl'
                      id='image'
                      placeholder=''
                      className='form-control'
                      value={tempData.imageUrl}
                      onChange={handleChange}
                    />
                  </label>
                  <div className='form-group mb-3'>
                    <label className='w-100' htmlFor='customFile'>
                       
                      <input
                        type='file'
                        name="file-to-upload"
                        id='customFile'
                        className='form-control'
                        onChange={handleFileChange} // 
                        ref={fileInputRef}
                      />
                    </label>
                  </div>
                  <h4 className="mt-5"></h4>
                  {tempData.imagesUrl && tempData.imagesUrl.map((url, index) => ( //  tempData.imagesUrl
                    <div key={index} className='mb-2'>
                      <label htmlFor={`image-url-${index}`} className='form-label'> #{index + 1}</label>
                      <input
                        type='text'
                        className='form-control'
                        id={`image-url-${index}`}
                        value={url}
                        onChange={(e) => handleImageUrlChange(index, e)}
                        placeholder=''
                      />
                    </div>
                  ))}
                  <button type='button' className='btn btn-info mt-2' onClick={addImageUrlInput}></button>
                </div>
                
                <div className='form-group my-3'>
                  <label htmlFor='imagesUrl' className='form-label'></label>
                  <input
                    type='file'
                    className='form-control'
                    id='imagesUrl'
                    multiple
                    onChange={handleMultipleFilesChange}
                    ref={multipleFilesInputRef} //  ref
                  />
                  
                </div>
                <span className="d-block mt-2 text-muted">
                   jpgjpeg  png  3MB 
                </span>
              </div>
              <div className='col-sm-3'>
                <div className='mb-3'>
                  {/*  */}
                  <span className="d-block"></span>
                  {isMainImageLoading ? ( // 
                    <LoaderGrid />
                  ) : (
                    <img src={tempData.imageUrl || uploadPreviewUrl} alt="preview" className="img-fluid img-thumbnail" />
                  )}
                  
                  {/*  */}
                  <div className='mt-2'>
                    <span className="d-block"></span>
                    {isOtherImagesLoading ? ( // 
                      <LoaderGrid />
                    ) : (
                      tempData.imagesUrl && tempData.imagesUrl.map((url, index) => (
                        <img key={index} src={url} alt={`preview ${index}`} className="img-thumbnail" style={{ maxWidth: '120px', marginRight: '5px' }} />
                      ))
                    )}
                  </div>
                </div>
              </div>
              <div className='col-sm-6'>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='title'>
                    
                    <input
                      type='text'
                      id='title'
                      name='title'
                      placeholder=''
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.title}
                    />
                  </label>
                </div>
                <div className='row'>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='category'>
                      
                      <input
                        type='text'
                        id='category'
                        name='category'
                        placeholder=''
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.category}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='unit'>
                      
                      <input
                        type='unit'
                        id='unit'
                        name='unit'
                        placeholder=''
                        className='form-control'
                        onChange={handleChange}
                        value={tempData.unit}
                      />
                    </label>
                  </div>
                </div>
                <div className='row'>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='origin_price'>
                      
                      <input
                        type='number'
                        id='origin_price'
                        name='origin_price'
                        placeholder=''
                        className='form-control'
                        min="0"
                        onChange={handleChange}
                        value={tempData.origin_price}
                      />
                    </label>
                  </div>
                  <div className='form-group mb-2 col-md-6'>
                    <label className='w-100' htmlFor='price'>
                      
                      <input
                        type='number'
                        id='price'
                        name='price'
                        placeholder=''
                        className='form-control'
                        min="0"
                        onChange={handleChange}
                        value={tempData.price}
                      />
                    </label>
                  </div>
                </div>
                <hr />
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='description'>
                    
                    <textarea
                      type='text'
                      id='description'
                      name='description'
                      placeholder=''
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.description}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <label className='w-100' htmlFor='content'>
                    
                    <textarea
                      type='text'
                      id='content'
                      name='content'
                      placeholder=''
                      className='form-control'
                      onChange={handleChange}
                      value={tempData.content}
                    />
                  </label>
                </div>
                <div className='form-group mb-2'>
                  <div className='form-check'>
                    <label
                      className='w-100 form-check-label'
                      htmlFor='is_enabled'
                    >
                      
                      <input
                        type='checkbox'
                        id='is_enabled'
                        name='is_enabled'
                        placeholder=''
                        className='form-check-input'
                        onChange={handleChange}
                        checked={!!tempData.is_enabled}
                      />
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='modal-footer'>
            <button type='button' onClick={handleCloseAndClear} className='btn btn-secondary'>
              
            </button>
            <button type='button' className='btn btn-primary'
              onClick={submit}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProductModal;