import { Container, Row, Col, Card, Button } from 'react-bootstrap';
import logo4 from "../images/logo4.png";
import { useDispatch } from 'react-redux';
import { createAsyncMessage } from '../slice/messageSlice';
import { useState } from 'react';

const CouponCard = ({ coupon }) => {
  const dispatch = useDispatch();
  const [isDisabled, setIsDisabled] = useState(false); // 

  const handleCopy = async (code) => {
    setIsDisabled(true);
    try {
      await navigator.clipboard.writeText(code);
      //  action
      dispatch(createAsyncMessage({ success: true, message: `: ${code}` }));
    } catch (error) {
      dispatch(createAsyncMessage({ success: false, message: "" }));
    }

    setTimeout(() => setIsDisabled(false), 3000);  // 3
  };

  return (
    <Card className={`card-coupon cardCoupon`}>
      {/*  */}
      <div className={`circle left`}></div>
      <Card.Body className="d-flex align-items-center">
        <Card.Img 
          variant="top" 
          src={coupon.logo} 
          style={{ width: '25%'}} 
          className="ms-3"
          />
        {/*  */}
        <div className="divider"></div>
        <div className="ms-5">
          <Card.Title className="fs-6">{coupon.title}</Card.Title>
          <Card.Text className="mb-1"><span className="h4">{coupon.description}</span> </Card.Text>
          <Card.Text>
            {coupon.code}
            <Button 
              variant="outline-secondary" 
              size="sm" 
              onClick={() => handleCopy(coupon.code)}
              className="ms-1"
              disabled={isDisabled}
            >
              <i className="bi bi-files"></i>
            </Button>
          </Card.Text>
        </div>
      </Card.Body>
      {/*  */}
      <div className={`circle right`}></div>
    </Card>
  );
};

export const MyCoupons = () => {
  // 
  const coupons = [
    {
      id: 1,
      title: '',
      description: '8',
      code: 'REDPACKET80',
      logo: logo4,
    },
    {
      id: 2,
      title: '',
      description: '3',
      code: 'NEWYEAR30',
      logo: logo4,
    },
    // ... 
  ];

  return (
    <Container>
      <Row>
        <Col className="d-flex align-items-center flex-column">
          {coupons.map((coupon) => (
            <CouponCard key={coupon.id} coupon={coupon} />
          ))}
        </Col>
      </Row>
    </Container>
  );
};

export default MyCoupons;
